"use-client"

import {
    Card,
    CardContent,
    CardHeader,
} from "@/components/ui/card";
import Image from "next/image";
import Cart from "@/assets/seller-imgs/cart-symbol.png";
import Cash from "@/assets/seller-imgs/cash-symbol.png";
import Clock from "@/assets/seller-imgs/clocksymbol.png";
import Package from "@/assets/seller-imgs/package-symbol.png";

export function CardActivity(){

    return(
        <section className="flex flex-wrap gap-4 items-center justify-center mb-3">
            <div>
            {/* Cash */}
            <Card className="w-full min-w-[250px] max-w-[300px] min-h-[150px] flex flex-col justify-between">
            <CardHeader>
                <Image
                    src={Cash}
                    alt="Cash"
                    width={35}
                    height={35}
                />
            </CardHeader>
            <CardContent className="flex flex-col">
                <h5>Total Sales</h5>
                <h5>Hardcoded</h5>
            </CardContent>
            </Card>
            </div>

            <div>
            {/* Orders */}
            <Card className="w-full min-w-[250px] max-w-[300px] min-h-[150px] flex flex-col justify-between">
            <CardHeader>
                <Image
                    src={Cart}
                    alt="Cart"
                    width={35}
                    height={35}
                />
            </CardHeader>
            <CardContent className="flex flex-col">
                <h5>Total Orders</h5>
                <h5>Hardcoded</h5>
            </CardContent>
            </Card>
            </div>

            <div>
            {/* Pending */}
            <Card className="w-full min-w-[250px] max-w-[300px] min-h-[150px] flex flex-col justify-between">
            <CardHeader>
                <Image
                    src={Clock}
                    alt="Clock"
                    width={35}
                    height={35}
                />
                
            </CardHeader>
            <CardContent className="flex flex-col ">
                <h5>Pending Orders</h5>
                <h5>Hardcoded</h5>
            </CardContent>
            </Card>
            </div>

            <div>
            {/* Products */}
            <Card className="w-full min-w-[250px] max-w-[300px] min-h-[150px] flex flex-col justify-between">
            <CardHeader>
                <Image
                    src={Package}
                    alt="Package"
                    width={35}
                    height={35}
                />
                
            </CardHeader>
            <CardContent className="flex flex-col">
                <h5>Total Products</h5>
                <h5>Hardcoded</h5>
            </CardContent>
            </Card>
            </div>
            
        </section>
        
    )
}